@media screen and (min-width: 700px) {
    .footer-sections {
        display: flex;
    }
}

.footer {
    /* position: absolute; */
    /* bottom: 0;
  width: 100%;
  height: 12em; */
    grid-column: full-start / full-end;
    padding: 1rem;
    background-color: rgba(0, 115, 181);
    flex: 6;
}

.nav {
    list-style: none;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(10rem, 1fr));
    grid-gap: 1rem;
    align-items: center;
}

.nav__link:link,
.nav__link:visited {
    /* margin-top: 4rem; */
    margin-right: 2rem;
    margin-top: 3.25em;
    font-size: 1.4rem;
    color: #fff;
    text-decoration: none;
    text-transform: uppercase;
    text-align: center;
    padding: 0.5rem;
    display: block;
}

.nav__link:hover,
.nav__link:active {
    background-color: rgba(255, 255, 255, 0.05);
    transform: translateY(-3px);
    transition: all 0.5s ease;
    border-radius: 0.25em;
}

.second-row-footer {
    display: grid;
}

.copyright {
    font-size: 1rem;
    color: lightgray;
    margin-top: 1rem;
    margin-right: auto;
    margin-left: auto;
    margin-bottom: 2em;
    text-align: center;
    width: 70%;
}

.social-media-logo {
    width: 4%;
    margin: 0.5em;
    float: left;
}

.social-logos {
    margin-top: 1em;
    margin-left: 5.5em;
    margin-bottom: -2.5em;
}

@media screen and (max-width: 630px) {
    .social-media-logo {
        width: 10%;
        margin: 0.5em;
        float: right;
    }
    .social-logos {
        margin-top: -4em;
        margin-right: 3.5em;
        /* margin-left: 5.5em; */
    }
}

.social-media-logo:hover,
.social-media-logo:active {
    transform: translateY(-3px);
    transition: all 0.5s ease;
    box-shadow: -0.25em 0.5em 0.5em rgba(0, 0, 0, 0.25);
}

.contributing-writer {
    /* not going to allow growth or shrinkage: */
    flex: 0 0 10em;
    background-color: rgba(206, 79, 39);
    background-image: url("SOSD.svg");
    background-position: center;
    background-repeat: no-repeat;
    /* background-position-x: 99%;
  background-position-y: 90%; */
    background-size: 60%;
}

@media screen and (max-width: 615px) {
    .contributing-writer {
        background-size: 30%;
    }
}

@media screen and (max-width: 700px) {
    .contributing-writer {
        background-size: 20%;
    }
}

.contributing-writer h2 {
    padding: 2em;
    padding-top: 3.5em;
    font-size: 1.4rem;
    text-transform: uppercase;
    text-align: center;
    margin-top: 0;
    margin-bottom: 0;
}

.contributing-writer h2:hover,
.contributing-writer h2:active {
    transform: translateY(-5px);
    text-shadow: 2px 2px rgba(0, 115, 181);
    transition: all 0.5s ease;
}

.contributing-writer a {
    text-decoration: none;
    color: #fff;
}

.admin-login {
}

.admin-login:hover,
.admin-login:active {
    background: rgba(255, 255, 255, 0.25);
    padding: 0.5em;
    border-radius: 0.25em;
}
